.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.searchcard
{
  background-color: #61dafb;

}
.newspage
{
  padding: 10px;
  margin-left: 80px;
  margin: 10px;
  width: 300px;
  grid-template-columns: auto auto auto;
  background-color: rgb(161, 228, 161);
  box-shadow: 2px 5px 10px black;
}
.newscard
{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 80px; /* Add some space between items */
}
.img
{
  height: 300px;
  width: 300px;
  background-image: url("data:image/jpeg;base64,");
}
h3{
  color: red;
}
h2{
  padding: 10px;
  margin-left: 10px;
  margin: 10px;
  width: fit-content;
  grid-template-columns: auto auto auto;
  background-color: rgb(157, 236, 232);
  box-shadow: 2px 5px 10px black;
}
h1{
  align-items: left;
}
.search{
  display: flex;
  gap: 750px;
  background-color:#61dafb;
  margin-bottom: 50px;
  padding: 10px;
  box-shadow: 5px 10px 15px rgb(10, 9, 9);
  border-radius: 25px;
 
}
input{
  border-radius: 25px;
  padding-left: 20px;
  font-size: 16px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}